.lc-accordion {
  color: #e4f4ff;
  background-color: rgba(0, 0, 0, 0);
}
.lc-accordion .accordion-header {
  font-size: 14px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #e4f4ff;
  cursor: pointer;
  padding: 10px 0;
  width: 100%;
  text-align: left;
  border-bottom: none;
  outline: none;
  transition: 0.4s;
}
.lc-accordion .accordion-header .title-switch .accordion-icon {
  transition: transform 0.2s;
  transform: rotate(0deg);
  animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.lc-accordion .accordion-active,
.lc-accordion .accordion-header:hover {
  background-color: rgba(0, 0, 0, 0);
}
.lc-accordion .accordion-active .title-switch .accordion-icon {
  transition: transform 0.2s;
  transform: rotate(90deg);
  animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.lc-accordion .lc-accordion-body {
  display: flex;
  flex-wrap: wrap;
  padding: 0 5px;
  background-color: rgba(0, 0, 0, 0);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
