.lc-theme-editor {
  width: 860px;
  height: 600px;
  padding: 7px;
  display: flex;
}
.lc-theme-editor .editor-left {
  padding: 5px;
  width: 460px;
}
.lc-theme-editor .editor-left .theme-operate-btn {
  display: flex;
  justify-content: space-between;
}
.lc-theme-editor .editor-left .theme-operate-btn .lc-button {
  width: 100%;
  margin-right: 5px;
}
.lc-theme-editor .editor-right {
  width: 380px;
  padding: 5px;
  overflow-y: scroll;
}
