.lc-background-config {
  font-size: 12px;
  color: #00ebff;
}
.lc-background-config .lc-bg-upload {
  width: 100%;
  height: 187px;
  padding: 2px;
  border-radius: 1px;
  box-sizing: border-box;
  border: solid 1px #5a878c;
}
.lc-background-config .lc-bg-upload .lc-upload-content {
  width: 100%;
  height: 100%;
  border: 0;
}
.lc-background-config .lc-bg-upload .lc-upload-content img {
  width: 100%;
  height: 100%;
}
.lc-background-config .lc-bg-upload .lc-upload-content .ant-upload.ant-upload-drag {
  border: 0;
  background-color: rgba(46, 77, 88, 0.27);
}
.lc-background-config .lc-bg-upload .lc-upload-content .ant-upload.ant-upload-drag .ant-upload-drag-container {
  color: #c6c9cd;
  font-size: 12px;
}
