.radio-buttons {
  display: flex;
  flex-direction: row;
}
.radio-buttons .radio-button {
  display: flex;
  align-items: center;
  margin-right: 10px;
  cursor: pointer;
}
.radio-buttons .radio-button input[type="radio"] {
  display: none;
}
.radio-buttons .radio-button .radio-circle {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #aaa;
  position: relative;
  margin-right: 5px;
}
.radio-buttons .radio-button .radio-circle::before {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #ddd;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.2s ease-in-out;
}
.radio-buttons .radio-button input[type="radio"]:checked + .radio-circle::before {
  transform: translate(-50%, -50%) scale(1);
}
.radio-buttons .radio-button input[type="radio"]:checked + .radio-circle::before {
  background-color: #aeaeae;
}
.radio-buttons .radio-button .radio-label {
  color: #c6c9cd;
  font-size: 12px;
  font-weight: bold;
}
.radio-buttons .radio-button:hover .radio-circle {
  border-color: #555;
}
.radio-buttons .radio-button:hover input[type="radio"]:checked + .radio-circle::before {
  background-color: #555;
}
