.lc-select-container {
  display: inline-block;
  width: 100%;
}
.lc-select-container .lc-select-header {
  background: #1b1e23;
  color: #b2b2b2;
  padding: 4px 0 4px 5px;
  width: 100%;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  user-select: none;
  border: 1px solid #1b1e23;
}
.lc-select-container .lc-select-header:hover {
  border: 1px solid rgba(86, 86, 86, 0.77);
}
.lc-select-container .lc-select-options {
  color: #b2b2b2;
  list-style-type: none;
  border-radius: 2px;
  background: #4b4f54;
  padding: 5px 0;
  margin: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  max-height: 200px;
  overflow-y: scroll;
}
.lc-select-container .lc-select-options li {
  background: #4b4f54;
  padding: 6px 0 6px 5px;
  transition: all 0.3s ease-in-out;
  user-select: none;
  width: 100%;
}
.lc-select-container .lc-select-options li:hover {
  background: #71717f;
  cursor: pointer;
}
.lc-select-container .placeholder {
  color: #c6c9cd;
}
